#container {
    height: 420px;
}

.highcharts-figure,
.rrze-statistik-table table {
    min-width: 300px;
    max-width: 820px;
    margin: 1em auto;
}

.rrze-statistik-table table {
    border-collapse: collapse;
    border: 1px solid #ebebeb;
    margin: 10px auto;
    text-align: center;
    width: 100%;
    font-size: 13px;
}

.rrze-statistik-table caption {
    padding: 1em 0;
    font-size: 1.2em;
    color: #555;
}

.rrze-statistik-table th {
    font-weight: 600;
    padding: 0.5em;
}

.rrze-statistik-table td,
.rrze-statistik-table th,
.rrze-statistik-table caption {
    padding: 0.5em;
}

.rrze-statistik-table thead tr,
.rrze-statistik-table tr:nth-child(even) {
    background: #f8f8f8;
}

.rrze-statistik-table tr:hover {
    background: #f1f7ff;
}

.rrze-statistik-table{
    table{
        width: 100%;
        a{
            word-break: break-all;
        }
    }
    font-size: 1em;
    th, td {
        text-align: left;
    }
}

